$color: green;
$bgcol: white;
$font-size: 14px;
$padding: 5px;

body {
    .test {
        font-size: $font-size;
        line-height: 2 * $font-size;
    }
    .test {
        color: $color;

        &::after {
            content: "";
            background-color: $bgcol;
        }
    }
}

body .test {
    padding: $padding;
}
